@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center, header{
    display: flex;
    flex-direction: column;
    align-items: center;
    color:white;
}

header{    
    width:100%;
    color:white;
    font-size: 3rem;
    font-weight: 600;
}

.form{
    display: flex;
    flex-direction: column;
    width:150rem;
    align-items: center;
}
.input-name{
    margin-top: 20px;
    display: flex;
    align-items: baseline;
}
input {
    margin-left: 10px;
    outline: none;
    border: none;
    border-bottom: thin solid #ccc;
    background: transparent;
    font-size: 30px;
    color:white;
}
.submit-name{
    padding: 5px;
    font-family:Verjdana, Geneva, Tahoma, sans-serif;
    margin-top: 20px;
    font-size: 30px;
}
@media(max-width: 768px){
    .form{
        width:10rem;
    }
    .input-name{
        margin-top: -5px;
        font-size: 0.75rem;
        flex-direction: column;
        align-items: center;
    }
    .input-name > h1{
        font-weight: 200;
    }
    input{
        margin-top: 10px;
        font-size: 1.5rem;
    }
    .submit-name{
        font-size: 20px;
    }
    header{
        font-size: 2.25rem;
    }
}
main{
    min-height: 100vh;
    height:auto;
    background: black;
    /* background: linear-gradient(to right top,#040303,#580E28); */
    display: flex;
    align-items: center;
    justify-content: center;
}
 #home{
    position: fixed;
    z-index: 8;
    width:100%;
}
.circle1{
    -webkit-backdrop-filter: blur(2rem);
            backdrop-filter: blur(2rem);
    position:absolute;
    bottom:20%;
    left:-10%;
    background: white;
    background: linear-gradient(to right , #66ff00, #a8000ba4);
    border-radius: 50%;
    height:80vh;
    width:80vh;
    z-index: 2;
}
.circle2{
    top:-10%;
    right: -15%;
    position:fixed;
    background: white;
    background: linear-gradient(to left bottom, #A09Fa0, #b17b7a21);
    border-radius: 50%;
    height:80vh;
    width:80vh;
    z-index: 2;
    background-blend-mode:overlay;
}   
.section{
    /* background: linear-gradient(to right top,#131010a1,#580e285e); */
    -webkit-backdrop-filter: blur(5rem);
            backdrop-filter: blur(5rem);
    border-radius: 20px;
    /* background-color:#131010 ; */
    display: flex;
    align-items: center;
    justify-content: center;
    color:white;
    /* background: linear-gradient(to right top,#040303,orange); */
    width:50vw;
    /* min-width:50vw; */
    height:60vh;
    position: fixed;
    left:25%;
    flex-direction: column;
    padding:0px 20px;
}
#glass2{
    height:100vh;
    position: fixed;
    /* background: linear-gradient(to right top,#131010a1,#580e285e); */
    -webkit-backdrop-filter: blur(2rem);
            backdrop-filter: blur(2rem);
    color:white;
    width: 100vw;
    z-index: 5;
}
.question, .quesNo{
    font-family:'Roboto', sans-serif;
    font-size: 3.5rem;
}
.options{
    font-family:'Roboto', sans-serif;
}
.option{
    margin-top: 20px;
    margin-bottom: 10px;
    padding: 10px;
    border:1px solid rgb(54, 54, 54);
    border-radius: 10px;
    cursor: pointer;
    font-size: 2.5rem;
    background: black;
    color:white;
    min-width: 40vw;
}
.option:hover{
    background:rgb(51, 51, 51);
    
}
.next, .reset{
    font-size: 2rem;
    align-self: flex-end;
    margin-right: 4rem;
    padding: 10px;
    margin-top: 10px;
    display:none;
    border-radius: 10%;
    background-color: tomato;
    color:white;
    cursor: pointer;
}
.next{
    margin-bottom:-100px ;
}
.reset{
    display: block;
    margin-top: 5rem;
    align-self: center;
    margin-left: 5rem;
}
.score{
    z-index: 10;
}
.endscreen{
    position: fixed;
    -webkit-backdrop-filter: blur(5rem);
            backdrop-filter: blur(5rem);
    border-radius: 20px;
    /* background-color:#131010 ; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color:white;
    min-width: 50vw;
    width:auto;
    height:60vh;
    position: fixed;
    left:25%;
    flex-direction: column;
}

@media(max-width:768px){
    .circle1,.circle2{
        height:40vh;
        width:40vh;
    }
    .circle1{
        left:0%;
    }
    .circle2{
        right:0%;
    }
    .section{
        width:95%;
        left: 0%;
        margin-left: 15px;
        margin-top: 10px;
        /* height:60vh; */
        overflow-y: auto;
    }
    .question, .quesNo{
        font-size: 1.75rem;
        margin-top: 2rem;
        
    }
    .option{
        font-size: 1.05rem;
        width:80vw;
        margin-top: 10px;
    }
    main{
        height: 100%;
    }
    .endscreen{
        left: 0%;
        width:100%;
    }
}

